import React, { Component } from "react";
import Burger from "../../components/Burger/Burger";
import Aux from "../../hoc/Aux";
import BuildControls from "../../components/Burger/BuildControls/BuildControls";
import Modal from "../../components/UI/Modal/Modal";
import OrderSummary from "../../components/Burger/OrderSummary/OrderSummary";
import axios from "../../axios-orders";
import Spinner from "../../components/UI/Spinner/Spinner";
import withErrorHandler from "../../hoc/withErrorHandler/withErrorHandler";
import { connect } from "react-redux";
import * as actions from "../../store/actions/index";

class BurgerBuilder extends Component {
  state = {
    purchasing: false,

    defaultIngredients: null
  };

  componentDidMount() {
    this.props.onResetState();
    this.props.OnResetOrderPurchased();
    this.props.onInitIngredients();
    this.props.onSetAuthRedirect("/");
  }

  purchaseHandler = () => {
    //this.props.onBurgerBuildingTrue();
    if (this.props.isAuth) this.setState({ purchasing: true });
    else {
      this.props.onSetAuthRedirect("/checkout");
      this.props.history.push("/auth");
    }
  };

  purchaseCancelHandler = () => {
    this.setState({ purchasing: false });
  };

  purchaseContinueHandler = () => {
    this.props.onInitPurchase();
    this.props.history.push("/checkout");
  };

  updatePurchaseState(ingredients) {
    /* const sum = Object.keys(ingredients)
      .map(igKey => {
        return ingredients[igKey];
      })
      .reduce((sum, el) => {
        return sum + el;
      }, 0);
    this.setState({ purchasable: sum > 0 }); */
  }
  componentWillUpdate() {
    this.props.onUpdateIngredients();
  }

  render() {
    const disabledInfo = {
      ...this.props.ings
    };
    for (let key in disabledInfo) {
      disabledInfo[key] = disabledInfo[key] <= 0;
    }
    let orderSummary = null;

    let burger = this.props.error ? (
      <p>Ingredients can't be loaded!</p>
    ) : (
      <Spinner />
    );
    if (this.props.ings) {
      burger = (
        <Aux>
          <Burger ingredients={this.props.ings} burgerName={this.state.name} />
          <BuildControls
            ingredientAdded={this.props.onIngredientAdded}
            ingredientRemoved={this.props.onIngredientRemoved}
            disabled={disabledInfo}
            price={this.props.price}
            ordered={this.purchaseHandler}
            purchasable={this.props.purchasable}
            isAuth={this.props.isAuth}
          />
        </Aux>
      );
      orderSummary = (
        <OrderSummary
          ingredients={this.props.ings}
          price={this.props.price}
          clickedContinue={this.purchaseContinueHandler}
          clickedCancel={this.purchaseCancelHandler}
        />
      );
    }

    return (
      <Aux>
        <Modal
          show={this.state.purchasing}
          modalClosed={this.purchaseCancelHandler}
        >
          {orderSummary}
        </Modal>
        {burger}
      </Aux>
    );
  }
}

const mapStateToProps = state => {
  return {
    ings: state.burgerBuilder.ingredients,
    price: state.burgerBuilder.totalPrice,
    purchasable: state.burgerBuilder.purchasable,
    error: state.burgerBuilder.error,
    resetstate: state.burgerBuilder.resetstate,
    purchased: state.order.purchased,
    token: state.auth.token,
    isAuth: state.auth.token !== null,
    building: state.burgerBuilder.building
  };
};
const mapDispatchToProps = dispatch => {
  return {
    onIngredientAdded: ingName => dispatch(actions.addIngredient(ingName)),
    onIngredientRemoved: ingName => dispatch(actions.removeIngredient(ingName)),
    onUpdateIngredients: () => dispatch(actions.updateIngredients()),
    onResetState: () => dispatch(actions.resetState()),
    onInitIngredients: () => dispatch(actions.initIngredients()),
    onLastOrderIngredients: () => dispatch(actions.lastOrderIngredients()),
    onInitPurchase: () => dispatch(actions.purchaseInit()),
    OnResetOrderPurchased: () => dispatch(actions.resetOrderPurchased()),
    onSetAuthRedirect: path => {
      dispatch(actions.setAuthRedirect(path));
    }
    //onBurgerBuildingTrue: () => dispatch(actions.burgerBuildingTrue())
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withErrorHandler(BurgerBuilder, axios));
