import React from "react";
import classes from "./Backdrop.module.css";

const BackDropMenu = props =>
  props.show ? (
    <div className={classes.BackdropMenu} onClick={props.clicked} />
  ) : null;

const BackDropModal = props =>
  props.show ? (
    <div className={classes.BackdropModal} onClick={props.clicked} />
  ) : null;

export { BackDropMenu, BackDropModal };
