import React, { Component } from "react";
import Order from "../../components/Order/Order";
import axios from "../../axios-orders";
import { connect } from "react-redux";
import withErrorHandler from "../../hoc/withErrorHandler/withErrorHandler";
import * as actions from "../../store/actions/index";
import Spinner from "../../components/UI/Spinner/Spinner";
class Orders extends Component {
  componentDidMount() {
    if (!this.props.token) {
      this.props.history.push("/");
    } else {
      this.props.onFetchOrders(this.props.token, this.props.userId);
    }
  }

  redirectOnCheckout(ingredients, price) {
    this.props.onOrderClicked(ingredients, price);
    this.props.history.replace("/checkout");
  }
  render() {
    let orders = <Spinner />;
    if (!this.props.token) {
      orders = <p style={{ textAlign: "center" }}>Log-in to see your orders</p>;
    }

    if (!this.props.loading) {
      orders = this.props.orders.map(order => (
        <Order
          key={order.id}
          ingredients={order.ingredients}
          price={order.price}
          name={order.orderData.name}
          clicked={() =>
            this.redirectOnCheckout(order.ingredients, order.price)
          }
        />
      ));
    }

    return <div>{orders}</div>;
  }
}

const mapStateToProps = state => {
  return {
    orders: state.order.orders,
    loading: state.order.loading,
    ingredients: state.burgerBuilder.ingredients,
    error: state.order.error === false,
    token: state.auth.token,
    userId: state.auth.userId
  };
};
const mapDispatchToProps = dispatch => {
  return {
    onFetchOrders: (token, userId) =>
      dispatch(actions.fetchOrders(token, userId)),
    onOrderClicked: (ingredients, price) =>
      dispatch(actions.orderClicked(ingredients, price))
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withErrorHandler(Orders, axios));
