export const initIngredients = () => {
  const lastorder = true;
  return dispatch => {
    axios
      .get("https://react-my-burger-e19d0.firebaseio.com/ingredients.json")
      .then(response => {
        dispatch(setIngredients(response.data));
        if (lastorder) {
          dispatch(lastOrderIngredients());
        }
      })
      .catch(error => {
        dispatch(fetchIngredientsFailed());
      });
  };
};

export const lastOrderIngredients = () => {
  return dispatch => {
    axios
      .get("https://react-my-burger-e19d0.firebaseio.com/orders.json")
      .then(response => {
        let lastElement = null;

        Object.keys(response.data).forEach(el => {
          lastElement = response.data[el];
        });
        Object.keys(lastElement.ingredients).forEach(el => {
          const quantity = lastElement.ingredients[el];
          for (let i = 0; i < quantity; i++) {
            dispatch(addIngredient(el));
          }
        });
      })
      .catch(error => {});
  };
};
